import React, { Component } from 'react';
import {
  Badge,
  Button,
  ButtonDropdown,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
  Table,
} from 'reactstrap';

class Profile extends Component {
  constructor(props) {
    super(props);

    //this.handleUploadFile = this.handleUploadFile.bind(this);
    this.toggleLoanInfo = this.toggleLoanInfo.bind(this);
    this.toggleLoanResults = this.toggleLoanResults.bind(this);
    this.state = {
      collapseNew: false,
      collapseAll: false,
      fadeIn: true,
      timeout: 300,
      status: 'Closed',
      applications: null,
      profileId: this.props.match.params.id
    };
  }

  toggleLoanInfo() {
    this.setState(
      { collapseNew: !this.state.collapseNew,
        collapseAll: false
      });
  }

  toggleLoanResults() {
    this.setState(
      { collapseNew: false,
        collapseAll: !this.state.collapseAll
      });
  }

  componentDidMount(){
    const data = new FormData()
    data.append('profileId', this.state.profileId)
    fetch('/api/applications/profile',{
        method: 'POST',
        body: data})
      .then(res => res.json())
      .then(applications => this.setState({applications:applications}, () => console.log("Fetched ")));
  }

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" lg="6" style={{margin:'0 auto'}}>
            <Card>
              <CardHeader>
                <Button color="primary" onClick={this.toggleNew} style={{ marginBottom: '1rem' }}>Application Info</Button>
              </CardHeader>
              <Collapse isOpen={this.state.collapseNew}>
                <CardBody>
                  <Table responsive>
                    <tbody>
                        {this.state.applications.map((application, index) => (
                          <tr>
                            <td><strong>Name :</strong></td>
                            <td>{application.First_Name} {application.Last_Name}</td>
                          </tr>
                        ))}
                    </tbody>
                  </Table>
                </CardBody>
              </Collapse>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Profile;



